<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process194"></a>

<p><br></p>
<p><span class="rvts17">午夜挖掘（</span><span class="rvts50">Midnight Oil</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:515,Y:178</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：瑞克（</span><span class="rvts20">Ryker</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">召唤出永恒者，并打败她，拾取永恒者石棺内的物品</span>
<br><span class="rvts83"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1228.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">该任务从瑞克这里接到，但以打败永恒者并拾取永恒者石棺内的物品而告终，无需交付任务。（详见黑坑洞探索篇）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



